.controlButtons{position:fixed;bottom:20px;right:20px;display:flex;flex-direction:column;gap:12px;z-index:1000}.controlButton{background:var(--white);border:2px solid var(--gray-light);border-radius:50%;width:56px;height:56px;font-size:20px;cursor:pointer;box-shadow:0 4px 12px rgba(0,0,0,.15);transition:all .3s ease;display:flex;align-items:center;justify-content:center}.controlButton:hover{transform:scale(1.1);border-color:var(--primary-light);box-shadow:0 6px 20px rgba(0,0,0,.25)}.controlButton.active{background:var(--primary);border-color:var(--primary);color:var(--white)}@media(max-width: 767px){.controlButtons{bottom:16px;right:16px;gap:8px}.controlButton{width:48px;height:48px;font-size:16px}}